import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";

const resources = {
  en: {
    translation: {
      switchLanguage: "",
      main: "Main",
      prices: "Prices",
      contactSales: "Contact sales",
      trackYourShipment: "Track your shipment",
      signIn: "Sign in",
      shipmentNumber: "Shipment number",
      // shipment status start
      CANCELLED: "Shipment has been canceled",
      DELIVERED: "Shipment has been delivered",
      CREATED: "Shipment has been created",
      TICKET_CREATED: "Ticket Created",
      PACKAGE_RECEIVED: "Package Received",
      IN_TRANSIT: "In Transit",
      OUT_FOR_DELIVERY: "Out for Delivery",
      WAITING_FOR_CUSTOMER_ACTION: "Waiting for Customer Action",
      NOT_YET_SHIPPED: "Not Yet Shipped",
      DELIVERED_TO_SENDER: "Delivered to Sender",
      CUSTOMER_NOT_IN_ADDRESS:
        "Retry delivery - the customer is not at the address",
      CUSTOMER_POSTPONED:
        "Postponed - the customer requested postponement for another day",
      CUSTOMER_NOT_ANSWERING: "Customer is not answering",
      // shipment status end
      lastUpdate: "Last update",
      merchantName: "Merchant name",
      deliveryDateWithin: "Delivery date within",
      shipmentCreate: "Your shipment has been created",
      shipmentReceivedFromMerchant: "Shipment has been received from merchant",
      shipmentIsOutForDelivery: "Shipment is out for delivery",
      deliveryIsDone: "Delivery is done",
      shipmentDetails: "Shipment details",
      deliveryAddress: "Delivery address",
      branch: "Branch",
      date: "Date",
      time: "Time",
      details: "Details",
      problem: "Is there's a problem in your shipment?",
      reportProblem: "Report a problem",
      noEvents: "No events to be displayed",
    },
  },
  ar: {
    translation: {
      switchLanguage: "ENG",
      main: "",
      prices: "",
      contactSales: "  ",
      trackYourShipment: " ",
      signIn: " ",
      shipmentNumber: " ",
      // shipment status start
      CANCELLED: "  ",
      DELIVERED: "  ",
      CREATED: "  ",
      TICKET_CREATED: "  ",
      PACKAGE_RECEIVED: "  ",
      IN_TRANSIT: " ",
      OUT_FOR_DELIVERY: " ",
      WAITING_FOR_CUSTOMER_ACTION: "   ",
      NOT_YET_SHIPPED: "   ",
      DELIVERED_TO_SENDER: "   ",
      CUSTOMER_NOT_IN_ADDRESS: "  -    ",
      CUSTOMER_POSTPONED: "  -     ",
      CUSTOMER_NOT_ANSWERING: "  ",
      // shipment status end
      lastUpdate: " ",
      merchantName: " ",
      deliveryDateWithin: "  ",
      shipmentCreate: "  ",
      shipmentReceivedFromMerchant: "    ",
      shipmentIsOutForDelivery: "   ",
      deliveryIsDone: " ",
      shipmentDetails: " ",
      deliveryAddress: " ",
      branch: "",
      date: "",
      time: "",
      details: "",
      problem: "    ",
      reportProblem: "  ",
      noEvents: "    ",
    },
  },
};

i18n
  .use(initReactI18next)
  .use(LanguageDetector)
  .init({
    resources,
    fallbackLng: "en",
    detection: {
      order: [
        "querystring",
        "cookie",
        "localStorage",
        "sessionStorage",
        "navigator",
        "htmlTag",
        "path",
        "subdomain",
      ],
    },
    interpolation: {
      escapeValue: false,
    },
  });

export default i18n;
